"use clien"
import React, { useState } from "react";
import {
  Box,
  TextField,
  Slider,
  Button,
  Typography,
  IconButton,
  InputAdornment,
  Collapse,
  List,
  ListItemButton,
} from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import MoreVertIcon from "@mui/icons-material/MoreVert";

const AccordionDropdown = ({ label, items, selectedValue, onChange }) => {
  const [open, setOpen] = useState(false);

  return (
    <Box sx={{ mb: 2 }}>
      <Button
        fullWidth
        variant="outlined"
        onClick={() => setOpen(!open)}
        sx={{ justifyContent: "space-between" }}
      >
        {selectedValue || label}
      </Button>
      <Collapse in={open}>
        <List>
          {items.map((item) => (
            <ListItemButton
              key={item.value}
              onClick={() => {
                onChange(item.value);
                setOpen(false);
              }}
            >
              {item.label}
            </ListItemButton>
          ))}
        </List>
      </Collapse>
    </Box>
  );
};

const AdvancedSearch = () => {
  const [distance, setDistance] = useState(50);
  const [price, setPrice] = useState([0, 8000]);
  const [region, setRegion] = useState("");
  const [moveInDate, setMoveInDate] = useState("");
  const [type, setType] = useState("");
  const [beds, setBeds] = useState("");
  const [baths, setBaths] = useState("");
  const [pets, setPets] = useState("");

  const handleSliderChange = (event, newValue) => {
    setPrice(newValue);
  };

  return (
    <Box p={3}>
      <Box
        sx={{
          padding: "30px",
          borderRadius: "8px",
          border: "2px solid #D8D8D8",
          bgcolor: "background.paper",
        }}
      >
        <Typography variant="h6" gutterBottom>
          Advanced Search
        </Typography>

        {/* Keyword Field */}
        <Box display="flex" alignItems="center" mb={2}>
          <TextField
            fullWidth
            variant="outlined"
            placeholder="Keyword"
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton>
                    <SearchIcon />
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
        </Box>

        {/* Location Field */}
        <TextField fullWidth variant="outlined" placeholder="Location" sx={{ mb: 2 }} />

        {/* Distance Slider */}
        <Typography gutterBottom>Distance: {distance} miles</Typography>
        <Slider
          value={distance}
          onChange={(e, value) => setDistance(value)}
          min={0}
          max={100}
          sx={{ mb: 2 }}
        />

        {/* Accordion Dropdowns */}
        <AccordionDropdown
          label="Regions"
          items={[
            { value: "region1", label: "Region 1" },
            { value: "region2", label: "Region 2" },
          ]}
          selectedValue={region}
          onChange={setRegion}
        />

        <AccordionDropdown
          label="Move-in Date"
          items={[
            { value: "date1", label: "Date 1" },
            { value: "date2", label: "Date 2" },
          ]}
          selectedValue={moveInDate}
          onChange={setMoveInDate}
        />

        <AccordionDropdown
          label="Type"
          items={[
            { value: "apartment", label: "Apartment" },
            { value: "house", label: "House" },
          ]}
          selectedValue={type}
          onChange={setType}
        />

        <AccordionDropdown
          label="Beds"
          items={[
            { value: "1", label: "1" },
            { value: "2", label: "2" },
          ]}
          selectedValue={beds}
          onChange={setBeds}
        />

        <AccordionDropdown
          label="Baths"
          items={[
            { value: "1", label: "1" },
            { value: "2", label: "2" },
          ]}
          selectedValue={baths}
          onChange={setBaths}
        />

        <AccordionDropdown
          label="Pets"
          items={[
            { value: "allowed", label: "Dogs Okay" },
            { value: "not-allowed", label: "Cats Okay" },
          ]}
          selectedValue={pets}
          onChange={setPets}
        />

        {/* Price Range Slider */}
        <Typography gutterBottom>
          From ${price[0]} to ${price[1]}
        </Typography>
        <Slider
          value={price}
          onChange={handleSliderChange}
          min={0}
          max={8000}
          sx={{ mb: 2 }}
        />

        {/* Advanced Options */}
        <Box display="flex" justifyContent="space-between" alignItems="center">
          <Typography variant="body2">Advanced</Typography>
          <IconButton>
            <MoreVertIcon />
          </IconButton>
        </Box>

        {/* Find Property Button */}
        <Button
          fullWidth
          variant="contained"
          sx={{
            mt: 2,
            bgcolor: "primary.main",
            color: "white",
            "&:hover": { bgcolor: "primary.dark" },
          }}
        >
          Find Property
        </Button>
      </Box>
    </Box>
  );
};

export default AdvancedSearch;
